import { ViewEncapsulation, Component, ViewChild, OnInit, OnDestroy, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { MatPaginator, MatSort, MatTableDataSource, MatDialog } from '@angular/material';
import { WorkoutsService } from '../../../core/auth/_services/workouts.service';
import { WorkoutAddEditComponent } from './workout-add-edit/workout-add-edit.component';
import { DomSanitizer } from '@angular/platform-browser';
import { Subscription } from 'rxjs';
import { MessageType, LayoutUtilsService } from '../../../core/_base/crud';
import {
  MatSnackBar,
  MatSnackBarConfig,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material';
@Component({
  selector: 'kt-workouts',
  templateUrl: './workouts.component.html',
  styleUrls: ['./workouts.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  //encapsulation: ViewEncapsulation.None,
})

export class WorkoutsComponent implements OnInit, OnDestroy {
  // Subscriptions
  private subscriptions: Subscription[] = [];
  private dataLength: Number;
  private categoryArr: String[];
  private Url: String;
  private club_id: String;


  ngOnInit(): void {
    this.club_id = JSON.parse(localStorage.getItem('user'))['pub_key'];
    this.getallWorkouts(this.club_id);
  }
  displayedColumns = ['No', '_id', 'Category', 'Name', 'Description', 'Image_url', 'Video_url', 'Actions'];
  dataSource: MatTableDataSource<workouts>;

  // @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  // @ViewChild(MatSort, {static: true}) sort: MatSort;
  // @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  constructor(private WorkoutsService : WorkoutsService ,public dialog: MatDialog 
    , private _sanitizer: DomSanitizer, private changeDetectorRefs: ChangeDetectorRef, private layoutUtilsService: LayoutUtilsService
    , private snackBar : MatSnackBar) {
    
      if (localStorage.getItem('user')) {
      try {
        let user = JSON.parse(localStorage.getItem('user'))
        this.Url = (user['baseurl']).trim()
      } catch (error) {

      }
}
  }
  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  getallWorkouts(club_key) {
    if(this.checkedpermission('getallworkouts')){
    this.WorkoutsService.getAllWorkouts(club_key).subscribe(
      res => {
        if (res['result']) {
          //console.log(res);
          this.dataSource = new MatTableDataSource(res['data']['workout']);
          this.categoryArr = res['data']['category'];
          this.dataLength = res['data']['workout'].length;
          // Assign the paginator *after* dataSource is set
          this.dataSource.paginator = this.paginator;
          // this.dataSource.sort = this.sort;
          //this.Url=res['data']['url'].trim();
          this.changeDetectorRefs.detectChanges();
        }
        else {
          let message = "No data avilable";
        }
      }
    );
  }
  }

  ngOnDestroy() {
    this.subscriptions.forEach(el => el.unsubscribe());
    // if (!this.changeDetectionRef['destroyed']) {
    //   this.changeDetectionRef.detectChanges();
    // }
  }


  popUpAddWorkout() {
    if(this.checkedpermission('addworkouts'))
    {
    const catArr = this.categoryArr;
    const dialogRef = this.dialog.open(WorkoutAddEditComponent, { data: { catArr } });
    dialogRef.afterClosed().subscribe(res => {
      if (res != undefined) this.getallWorkouts(this.club_id);
    });
  }
  else{
    this.open()
  }
  }
  popUpEditworkout(rowData) {
    if(this.checkedpermission('editworkouts'))
    {
    const catArr = this.categoryArr;
    const Url = this.Url;
    const dialogRef = this.dialog.open(WorkoutAddEditComponent, { data: { rowData, catArr, Url } });
    dialogRef.afterClosed().subscribe(res => {
      if (res != undefined) this.getallWorkouts(this.club_id);
    });
  }
  else
  {
    this.open()
  }
  }



  checkedpermission(key: String) {
    let checked = false
    if (localStorage.getItem('user')) {

      if (JSON.parse(localStorage.getItem('user'))) {
        let user = JSON.parse(localStorage.getItem('user'))

        if (user['allPrivilidge']) {
          const privilidge = user['allPrivilidge']

          Object.keys(privilidge).forEach(role => {
            Object.keys(privilidge[role]).forEach(pri => {

              let per = (privilidge[role][pri].toString()).trim()
              if (key.trim() == per) {
                checked = true
              }
            })
          })
        }
      }
    }

    return checked
  }


  open() {
    let message = 'Oh Snap ! You dont have permission to get this data';
    let actionButtonLabel = '';
    let action = true;
    let setAutoHide = true;
    let autoHide = 4000;
    let horizontalPosition: MatSnackBarHorizontalPosition = 'center';
    let verticalPosition: MatSnackBarVerticalPosition = 'top';

    let addExtraClass = false;
    let config = new MatSnackBarConfig();
    config.verticalPosition = verticalPosition;
    config.horizontalPosition = horizontalPosition;
    config.duration = setAutoHide ? autoHide : 0;
    //  config['extraClasses'] = addExtraClass ? ['warn-snackbar'] : undefined;
    config.panelClass = ['warn-snackbar'];
    this.snackBar.open(message, action ? actionButtonLabel : undefined, config);
  }

  deleteworkout(row) {
    if(this.checkedpermission('deleteworkouts'))
    {
    let id = row.id;
    const _title: string = 'Workout';
    const _description: string = 'Are you sure to permanently delete this Workout?';
    const _waitDesciption: string = 'Workout is deleting...';
    const _deleteMessage = 'Workout has been deleted';
    const dialogRef = this.layoutUtilsService.deleteElement(_title, _description, _waitDesciption);
    dialogRef.afterClosed().subscribe(res => {
      if (!res) { return; }
      else {
        this.WorkoutsService.deleteWorkouts(row['_id'], row['image_url']).subscribe(res => {
          //console.log(res);
          if (res['result']) {
            this.layoutUtilsService.showActionNotification(_deleteMessage, MessageType.Delete);
            this.getallWorkouts(this.club_id);
          }
          else {
            console.log("Error Deleting: ", res);
          }
        });
      }
    });
  }
  }
  private refreshTable() {
    this.paginator._changePageSize(this.paginator.pageSize);
  }

  santi_Url(videoURL: string) { if (videoURL) { return this._sanitizer.bypassSecurityTrustResourceUrl(videoURL); } }
  getUrlVideoId(videoURL: String) {
    var y = videoURL.split('/');
    var urlId = y[(y.length - 1)];
    //console.log(urlId);
    return 'https://img.youtube.com/vi/' + urlId + '/default.jpg';
  }



}

export interface workouts {
  _id: string; category: string; name: string; description: string;
  image_url: string; video_url: string;
}