// Angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
// NGRX
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
// Translate
import { TranslateModule } from '@ngx-translate/core';
import { PartialsModule } from '../../partials/partials.module';
// Services
import { HttpUtilsService, TypesUtilsService, InterceptService, LayoutUtilsService} from '../../../core/_base/crud';
// Shared
import { ActionNotificationComponent, DeleteEntityDialogComponent ,UpdateStatusDialogComponent } from '../../partials/content/crud';
import { CodePreviewModule } from '../../partials/content/general/code-preview/code-preview.module';
import { MaterialPreviewModule } from '../../partials/content/general/material-preview/material-preview.module';
//guard
import {PermissionGuard} from '../../../core/auth/_guards/permission.guard'
import { FlatpickrModule } from 'angularx-flatpickr';
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';
import { NgbModalModule } from '@ng-bootstrap/ng-bootstrap';
import { SharedModule } from '../sharing.module';
// Material
import {
	MatInputModule,
	MatPaginatorModule,
	MatProgressSpinnerModule,
	MatSortModule,
	MatTableModule,
	MatSelectModule,
	MatMenuModule,
	MatProgressBarModule,
	MatButtonModule,
	MatCheckboxModule,
	MatDialogModule,
	MatTabsModule,
	MatNativeDateModule,
	MatCardModule,
	MatRadioModule,
	MatIconModule,
	MatDatepickerModule,
	MatExpansionModule,
	MatTreeModule,
	MatAutocompleteModule,
	MAT_DIALOG_DEFAULT_OPTIONS,
	MatSnackBarModule,
	MatTooltipModule,
	MatFormFieldModule
} from '@angular/material';
import {
	usersReducer,
	UserEffects
} from '../../../core/auth';
// Components
import { WorkoutsComponent } from './workouts.component';
import { WorkoutAddEditComponent } from './workout-add-edit/workout-add-edit.component';
import { FileUploadModule } from 'ng2-file-upload';
//import { Ng2ImgMaxModule } from 'ng2-img-max';

const routes: Routes = [
	{
		path: '',
		component: WorkoutsComponent,
		children: [
			{
				path: 'workouts',
				component: WorkoutsComponent,
				data: { title: 'workouts' },
				canActivate: [PermissionGuard]
			}
		]
	}
];

@NgModule({
	imports: [
		CommonModule,
		HttpClientModule,
		SharedModule,
		PartialsModule,
		RouterModule.forChild(routes),
		StoreModule.forFeature('users', usersReducer),
    	EffectsModule.forFeature([UserEffects]),
		FormsModule,
		ReactiveFormsModule,
		TranslateModule.forChild(),
		MatButtonModule,
		MatMenuModule,
		MatSelectModule,
    	MatInputModule,
		MatTableModule,
		MatAutocompleteModule,
		MatRadioModule,
		MatIconModule,
		MatNativeDateModule,
		MatProgressBarModule,
		MatDatepickerModule,
		MatCardModule,
		MatPaginatorModule,
		MatSortModule,
		MatCheckboxModule,
		MatProgressSpinnerModule,
		MatSnackBarModule,
		MatExpansionModule,
		MatTabsModule,
		MatTooltipModule,
		MatDialogModule,
		CodePreviewModule,
		MaterialPreviewModule,
		MatTreeModule,
		NgbModalModule,
		FlatpickrModule.forRoot(),
		CalendarModule.forRoot({
			provide: DateAdapter,
			useFactory: adapterFactory
		}),

		MatFormFieldModule,	

		 FileUploadModule,
		// Ng2ImgMaxModule
	],
	providers: [
		InterceptService,
		{
        	provide: HTTP_INTERCEPTORS,
       	 	useClass: InterceptService,
			multi: true
		},
		{
			provide: MAT_DIALOG_DEFAULT_OPTIONS,
			useValue: {
				hasBackdrop: true,
				panelClass: 'kt-mat-dialog-container__wrapper',
				height: 'auto',
				width: '900px'
			}
		},
		HttpUtilsService,
		TypesUtilsService,
		LayoutUtilsService,
		PermissionGuard
	],
	entryComponents: [
		WorkoutAddEditComponent ,DeleteEntityDialogComponent ,UpdateStatusDialogComponent
	],
	declarations: [
		WorkoutsComponent ,WorkoutAddEditComponent ,
		 
	]
})
export class WorkoutsModule {}
