import { ViewEncapsulation,Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { Subscription, of, Observable } from 'rxjs';
import { MAT_DIALOG_DATA, MatDialogRef, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition, MatSnackBarConfig, MatSnackBar } from '@angular/material';
import { Workouts } from './workouts.model';
import { FormControl } from '@angular/forms';
import { startWith, map } from 'rxjs/operators';
import { FileUploader } from 'ng2-file-upload';
import { LayoutUtilsService, MessageType } from '../../../../core/_base/crud';
import { MembershipService } from '../../../../core/auth/_services';
// State
import { WorkoutsService } from '../../../../core/auth/_services/workouts.service';
import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';
import { Ng2ImgMaxService } from 'ng2-img-max';
@Component({
  selector: 'kt-workout-add-edit',
  templateUrl: './workout-add-edit.component.html',
  styleUrls: ['./workout-add-edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
})


export class WorkoutAddEditComponent implements OnInit , OnDestroy {
  private componentSubscriptions: Subscription;
  errormessage : string ='';
  viewLoading: boolean = false;
  loadingAfterSubmit : boolean = false ;
  hasFormErrors: boolean = false;

  workout : Workouts;
  workout$: any;

  control = new FormControl();
  categorys: string[];
  filteredcategorys: Observable<string[]>;
  private token= JSON.parse(localStorage.getItem('user'))['token'];
  public uploader: FileUploader = new FileUploader({url: this.ser.upload_file(), authToken : this.token ,  itemAlias: 'upload' });
  public imageprivew: any;
  public safe_video_url:any ='';
  
  constructor(public dialogRef: MatDialogRef<WorkoutAddEditComponent>,
  @Inject(MAT_DIALOG_DATA) public data: any , private router :Router ,
  private WorkoutsService : WorkoutsService,
  private ser :MembershipService,
  public snackBar: MatSnackBar,

  private layoutUtilsService: LayoutUtilsService ,
  private _sanitizer: DomSanitizer,
 private _ng2ImgMax : Ng2ImgMaxService
  ) { }

  ngOnInit() {
    const workoutModel= new Workouts();
    this.workout$ = of(workoutModel);
		this.workout$.subscribe(res => {
			if (!res) {return;}
			this.workout = res;
    });
    //if form ia add or edit 
    this.imageprivew = '';
    //load category input autocomplete 
    this.categorys=this.data['catArr'];
    // if form is edit exsist workout 
    if (typeof(this.data['rowData'])!= "undefined"){
      this.workout['id']=this.data['rowData']['_id'];
      this.workout.category=this.data['rowData']['category'];
      this.workout.name=this.data['rowData']['name'];
      this.workout.description=this.data['rowData']['description'];
      this.workout['old_image']=(this.data['rowData']['image_url']!='')?this.data['Url'].trim()+this.data['rowData']['image_url']:'';
      this.workout.image_url=(this.data['rowData']['image_url']!='')?this.data['Url'].trim()+this.data['rowData']['image_url']:'';
      this.imageprivew =(this.data['rowData']['image_url']!='')?this.data['Url'].trim()+this.data['rowData']['image_url']:'';
      this.workout.video_url=this.data['rowData']['video_url'];
      
      // assign video url to ifram >>>> 
      this.safe_video_url =(this.workout.video_url!='')?this.santi_Url(this.data['rowData']['video_url']):''; 
    }

  this.filteredcategorys = this.control.valueChanges.pipe(startWith(''),map(value => this._filter(value)));

  //upload Image   
  this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false; };
  }


  private _filter(value: string): string[] {
    const filterValue = this._normalizeValue(value);
    return this.categorys.filter(category => this._normalizeValue(category).includes(filterValue));
  }
  private _normalizeValue(value: string): string {return value.toLowerCase().replace(/\s/g, '');}
  
ngOnDestroy() {if (this.componentSubscriptions) {this.componentSubscriptions.unsubscribe();}}
getTitle(): string {return (typeof(this.data['rowData'])!= "undefined")? 'Edit Workout' : 'New Workout' ;}
/**
 * @param $event: Event
 */
onAlertClose($event) {this.hasFormErrors = false;}
  
handleUpload(event:any) {
  if (event.target.files && event.target.files[0]) {
    this.viewLoading = true;
    var reader = new FileReader();
    reader.onload = (event: ProgressEvent) => {this.imageprivew = (<FileReader>event.target).result;}
    reader.readAsDataURL(event.target.files[0]);

    this._ng2ImgMax.resizeImage(event.target.files[0], 600, 600).subscribe(
      result => {
        const newImage = new File([result], result.name);
        this.uploader.clearQueue();
        this.uploader.addToQueue([newImage]);
        //this.uploader.uploadAll();
        //console.log(result);
        reader.onload = (event: ProgressEvent) => {this.imageprivew = (<FileReader>event.target).result;}
        reader.readAsDataURL(event.target.files[0]);
        this.viewLoading = false;
      },
      error => console.log(error)
    );
  }
}
santi_Url(videoURL: string){if(videoURL ){return this._sanitizer.bypassSecurityTrustResourceUrl(videoURL);}}
convert_youtube(input) {
  // var pattern = /(?:http?s?:\/\/)?(?:www\.)?(?:youtube\.com|youtu\.be)\/(?:watch\?v=)?(\S+)/g;
  // if (pattern.test(input)) {
  //   //var replacement = '<iframe width="420" height="345" src="http://www.youtube.com/embed/$1" frameborder="0" allowfullscreen></iframe>';
  //   var replacement = 
  //   var input = input.replace(pattern, replacement);
  //   // For start time, turn get param & into ?
  //   var input = input.replace('&amp;t=', '?t=');
  // }
  // return input;
  if(input){
    const url = input.split(/(vi\/|v=|\/v\/|youtu\.be\/|\/embed\/)/);
    var ID = (url[2] !== undefined) ? url[2].split(/[^0-9a-z_\-]/i)[0] : url[0];
    input= 'https://www.youtube.com/embed/'+ID;
  }
  return input;
}

  onSubmit(input_image) {
    if (!this.workout.category && !this.workout.name && !this.workout.description) {
      this.hasFormErrors = true;
      this.errormessage = "Please Enter all Required Fileds"
    }
    else {
      this.hasFormErrors = false;
      this.viewLoading = true;
      
      let workoutObj = {};
      //add new workout 
      if (typeof (this.data['rowData']) == "undefined") {
        workoutObj = {
          "club_key": (JSON.parse(localStorage.getItem('user')))['lastloginaccount'],
          'category': this.workout['category'], 'name': this.workout['name'],
          'description': this.workout['description']
        };
        (this.workout['image_url']!='')? (workoutObj['image_url']=this.workout['image_url']) :'';
        (this.workout['video_url']!='')? (workoutObj['video_url']=this.convert_youtube(this.workout['video_url'])) :'';  
        //check if image not null >> add image and information >>> or add only information
        if(this.imageprivew!=''){
          this.uploader.uploadAll();
          this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
            var responsePath = JSON.parse(response);
            if (responsePath.result) {
              // this.workout.image_url = responsePath.image;
              workoutObj['image_url'] = responsePath.data;
              this.addWorkoutDataToDB(workoutObj);
            }
            else {this.errormessage = "Error While Upload Image";}
          };
        }
        else{this.addWorkoutDataToDB(workoutObj);}
        
      }
      else {
        // edit workout 
        //check if there are change
        if(this.workout.category==this.data['rowData']['category'] &&
          this.workout.name == this.data['rowData']['name'] &&
          this.workout.description == this.data['rowData']['description'] &&
          this.workout.video_url==this.data['rowData']['video_url'] &&
          input_image==''){
         //No CHANGE Happen
        console.log('No CHANGE Happen');
        }
        else{
          const _title: string = 'Workout';
          const _description: string = 'Are you sure to Edit this Workout?';
          const _waitDesciption: string = 'Workout is Editing...';
          const dialogRef = this.layoutUtilsService.RenwElement(_title, _description, _waitDesciption);
          dialogRef.afterClosed().subscribe(res => {if (!res) {return;}
          else {
              //there are change 
              const id=this.workout['id'];
              //edit and update image 
            if(this.workout.category!=this.data['rowData']['category']) workoutObj['category']=this.workout.category;
            if(this.workout.name!=this.data['rowData']['name']) workoutObj['name']=this.workout.name;
            if(this.workout.description!=this.data['rowData']['description']) workoutObj['description']=this.workout.description;
            if(this.workout.video_url!=this.data['rowData']['video_url']) workoutObj['video_url']=this.convert_youtube(this.workout.video_url);
              if(input_image!=''){
               // workoutObj['old_image'] = this.workout['image_url']; //to delete old image then add new one
               this.uploader.onBuildItemForm = (fileItem: any, form: any) => {
                form.append('old_image' , this.workout['image_url']);
               };
                //add new image
                this.uploader.uploadAll();
                this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
                  var responsePath = JSON.parse(response);
                  if (responsePath.result) {
                    // this.workout.image_url = responsePath.image;
                    workoutObj['image_url'] = responsePath.data;
                    this.editWorkoutDataToDB(workoutObj, id);
                  }
                  else { this.errormessage = "Error While Upload Image"; }
                };
              }
              else{
                //edit and not update image
                this.editWorkoutDataToDB(workoutObj, id);
              }
            }});
        }
        // console.log("workoutObj" ,workoutObj);
        // console.log("workout" ,this.workout);
        // console.log("input_image" ,input_image);
      }
      this.viewLoading = false;
    }
  }

  addWorkoutDataToDB(workoutObj){
    if(this.checkedpermission('addworkouts'))
    {
    this.WorkoutsService.addWorkouts(workoutObj).subscribe(
    res => {
      if (res['result']) {
        this.errormessage='Workout Added Successfully';
        this.redirctFunction(true);
      }
      else {
        this.errormessage = "No data Added , Server Error!";
        this.redirctFunction(false);
      }
    }
  );
    }
    else{
      this.open()
    }
  }
  editWorkoutDataToDB(workoutObj , id){
   if(this.checkedpermission('editworkouts'))
   {
        this.WorkoutsService.editWorkouts(workoutObj ,id).subscribe(
          res => {
            console.log(res);
            if (res['result']) {
              this.errormessage='Workout has been Updated Successfully';
              this.redirctFunction(true);
            }
            else {
              this.errormessage = "No data Edited , Server Error!";
              this.redirctFunction(false);
            }
          }
        );
   }
   else{
     this.open()
   }
    //   }
    // });
  }
  redirctFunction(done) {
    if (done) {
      this.dialogRef.close({});
      if (this.router.url == '/default/workouts')
        this.router.navigateByUrl("/default/workouts");
      else this.router.navigateByUrl("/workouts");
      setTimeout(() => {
        this.layoutUtilsService.showActionNotification( this.errormessage , MessageType.Update);
      }, 1000)
    } else {
      this.loadingAfterSubmit = false;
      this.viewLoading = false;
      this.hasFormErrors = true;
      //this.errormessage = 'Workout Doesn\'t Added';
    }
  }

  open() {
		let message = 'Oh Snap ! You dont have permission to get this data';
		let actionButtonLabel = '';
		let action = true;
		let setAutoHide = true;
		let autoHide = 4000;
		let horizontalPosition: MatSnackBarHorizontalPosition = 'center';
		let verticalPosition: MatSnackBarVerticalPosition = 'top';

		let addExtraClass = false;
		let config = new MatSnackBarConfig();
		config.verticalPosition = verticalPosition;
		config.horizontalPosition = horizontalPosition;
		config.duration = setAutoHide ? autoHide : 0;
		//  config['extraClasses'] = addExtraClass ? ['warn-snackbar'] : undefined;
		config.panelClass = ['warn-snackbar'];
		this.snackBar.open(message, action ? actionButtonLabel : undefined, config);
  }
  checkedpermission(key: String) {
		let checked = false
		if (localStorage.getItem('user')) {

			if (JSON.parse(localStorage.getItem('user'))) {
				let user = JSON.parse(localStorage.getItem('user'))

				if (user['allPrivilidge']) {
					const privilidge = user['allPrivilidge']

					Object.keys(privilidge).forEach(role => {
						Object.keys(privilidge[role]).forEach(pri => {

							let per = (privilidge[role][pri].toString()).trim()
							if (key.trim() == per) {
								checked = true
							}
						})
					})
				}
			}
		}

		return checked
	}


}
