import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { PublicMethods } from '../../_public/-public-methods'
const _url = PublicMethods.createURL

@Injectable({
  providedIn: 'root'
})
export class WorkoutsService {

  constructor(private http: HttpClient) { }

  getAllWorkouts(club_key){
    return this.http.post<any>(_url('workouts','getclubworkout','') ,{"club_key":club_key} )
  }
  
  addWorkouts(workoutObject){
     return this.http.post(_url('workouts','add', ''),workoutObject)
   }
  editWorkouts(workoutObject , id ){
    return this.http.patch(_url('workouts','editworkouts',id),workoutObject)
   }
   deleteWorkouts(id,image_url){
     return this.http.post(_url('workouts','deleteWorkouts',id),{'image_url':image_url} )
   }
   
}
